import React, { Component } from 'react';
import './App.css';
import New from './New'
import Table from './Table'
class App extends Component {
  constructor() {
    super();
    this.state = { books: [{
      title: "The Hobbit",
      author: "J.R.R. Tolkien",
      year: "1937",
      pages: "310",
      key: 7489,
      read: "Yes",
    },
    {
      title: "Dune",
      author: "Frank Herbert",
      year: "1965",
      pages: "412",
      key: 100,
      read: "Yes",
    },
    {
      title: "Hyperion",
      author: "Dan Simmons",
      year: "1989",
      pages: "482",
      key: 6378,
      read: "Yes",
    },
    {
      title: "Neuromancer",
      author: "William Gibson",
      year: "1984",
      pages: "271",
      key: 467,
      read: "Yes",
    }]
    }
    this.generateBookIndex = this.generateBookIndex.bind(this);
    this.addBook = this.addBook.bind(this);
    this.removeBook = this.removeBook.bind(this);
    this.toggleRead = this.toggleRead.bind(this);
  }
  generateBookIndex() {
    let newIndex = Math.floor(Math.random() * 9999);
    while (this.state.books.some(i => i.key) === newIndex) {
      newIndex = Math.floor(Math.random() * 9999);
    }
    return newIndex;
  }
  addBook(childData) {
    let newBookArray = this.state.books;
    newBookArray.push({
      title: childData.title,
      author: childData.author,
      year: childData.year,
      pages: childData.pages,
      key: this.generateBookIndex(),
      read: "No",
    });
    this.setState({books: newBookArray});
    console.log(newBookArray);
  }
  removeBook(childData) {
    let newBookArray = this.state.books.filter(i => i.key !== childData);
    this.setState({books: newBookArray});
  }
  toggleRead(childData) {
    let newBookArray = this.state.books;
    if (newBookArray[newBookArray.findIndex(i => i.key === childData)].read === "Yes") {
      newBookArray[newBookArray.findIndex(i => i.key === childData)].read = "No";
    } else {
      newBookArray[newBookArray.findIndex(i => i.key === childData)].read = "Yes";
    }
    this.setState({books:newBookArray});
  }
  render () {
  return (
    <div className="App">
     <New parentCallback = {this.addBook}/>
     <Table
      removeBookCallback = {this.removeBook}
      toggleReadCallback = {this.toggleRead}
      books = {this.state.books}
      />
    </div>
  );
  }
}

export default App;
